import React from 'react'
import { graphql, StaticQuery } from 'gatsby'
import moment from 'moment'
import PattyWorkingAtDesk from './patty-working-at-desk'
import Areas from './areas'
import Categories from './categories'
import { FacebookProvider, Group } from 'react-facebook'

const Footer = () => {
  return (
    <StaticQuery
      query={graphql`
        {
          site {
              siteMetadata {
                  title
                  description
              }
          }
        }
      `}
      render={data => {
        return (
          <footer className="container-fluid mt-5 pb-5 footer">
            <div className="row pt-5">
              <div className="col-lg mb-5 pr-5">
                <h2 className="title--header small mb-4"><a className="link brand" rel="author"
                                                       href="/">{data.site.siteMetadata.title}</a></h2>
                <p>
                  <small>{data.site.siteMetadata.description}</small>
                </p>
                <p><em>
                  <small>Alle regte voorbehou. &copy; helpmekaar.org {moment().format('YYYY')}</small>
                </em></p>
              </div>
              <div className="col-lg mb-5">
                <Areas />
              </div>
              <div className="col-lg mb-5">
                <Categories />
              </div>
              <div className="col-lg mb-5">

                <FacebookProvider appId="934192397029973">
                  <Group
                    href="https://www.facebook.com/groups/221643825770134"
                    width="280"
                    showSocialContext={true}
                    showMetaData={true}
                    skin="dark"
                  />
                </FacebookProvider>
              </div>
            </div>
          </footer>
        )
      }}/>
  )
}

export default Footer