import React from 'react';
import { graphql, StaticQuery } from 'gatsby'
import classnames from 'classnames'

const HowToList = ({ className }) => {
  return (
    <StaticQuery query={graphql`
    {
        howtos: allMarkdownRemark(filter: {frontmatter: {type: {eq: "howto"}}}, , sort: {fields: [frontmatter___title]}) {
            edges {
                node {
                    frontmatter {
                        title
                        description
                    }
                    fields {
                        slug
                    }
                }
            }
        }
    }
    `}
     render={data => {
       const howtos = data.howtos.edges.map(edge => edge.node);
       return (
         <div className={classnames('content-list', className)}>
           <h2 className="title--header small primary-colour mb-3">How to's</h2>
           <ul>
             {
               howtos.map((how, idx) => (
                 <li key={idx}><span className="spacer"/> <a href={how.fields.slug} className="link"> {how.frontmatter.title}</a></li>
               ))
             }
           </ul>
         </div>
       );
     }}
    />
  );
};

export default HowToList;