import React, { useState } from 'react';
import {
  Navbar,
  NavbarBrand,
  NavbarToggler,
  Nav,
  NavItem,
  NavLink,
  Collapse, UncontrolledDropdown, DropdownToggle, DropdownMenu,
} from 'reactstrap'
import { graphql, StaticQuery } from 'gatsby'
import DropdownItem from 'reactstrap/es/DropdownItem'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faFacebook } from '@fortawesome/free-brands-svg-icons'
import { FacebookProvider, Like } from 'react-facebook'

const Header = ({siteTitle, showTitle, hideFacebook}) => {

  const [isOpen, setOpen] = useState(false);

  return (
    <StaticQuery  query={graphql`
    {
        categories: allMarkdownRemark(filter: {frontmatter: {type: {eq: "category"}}}, sort: {fields: [frontmatter___name]}) {
            edges {
                node {
                    frontmatter {
                        tag
                        name
                        icon
                    }
                    fields {
                        slug
                    }
                }
            }
        }
        topLists: allMarkdownRemark(filter: {frontmatter: {type: {eq: "category"}}}, sort: {fields: [frontmatter___name]}) {
            edges {
                node {
                    frontmatter {
                        tag
                        name
                        icon
                    }
                    fields {
                        slug
                    }
                }
            }
        }
        howtos: allMarkdownRemark(filter: {frontmatter: {type: {eq: "howto"}}}, sort: {fields: [frontmatter___title]}) {
            edges {
                node {
                    frontmatter {
                        title
                    }
                    fields {
                        slug
                    }
                }
            }
        }
        areas: allMarkdownRemark(filter: {frontmatter: {type: {eq: "area"}}}, sort: {fields: [frontmatter___name]}) {
            edges {
              node {
                fields {
                  slug
                }
                frontmatter {
                  name
                  tag
                }
              }
            }
          }
        }

    `}
      render={data => {
        const categories = data.categories.edges.map(edge => edge.node);
        const areas = data.areas.edges.map(edge => edge.node);

        return (
        <header>
          <Navbar expand="lg" className="navbar-dark bg-transparent">
            {showTitle && (
              <NavbarBrand href="/">
                <h1 className="title--header brand">{siteTitle}</h1>
              </NavbarBrand>)}
            <NavbarToggler className="link light" onClick={() => setOpen(!isOpen)}/>
            <Collapse isOpen={isOpen} navbar>
              <Nav className="ml-auto" navbar>
                <NavItem>
                  <NavLink className="link light" href="/">Tuis</NavLink>
                </NavItem>

                <UncontrolledDropdown nav inNavbar>
                  <DropdownToggle nav caret className="link light">
                    Areas
                  </DropdownToggle>
                  <DropdownMenu right>
                    {
                      areas.map((area, i) => (
                        <DropdownItem key={i} href={ area.fields.slug }>{ area.frontmatter.name }</DropdownItem>
                      ))
                    }
                  </DropdownMenu>
                </UncontrolledDropdown>

                <UncontrolledDropdown nav inNavbar>
                  <DropdownToggle nav caret className="link light">
                    KategorieÃ«
                  </DropdownToggle>
                  <DropdownMenu right>
                    {
                      categories.map((cat, i) => (
                        <DropdownItem key={i} href={ cat.fields.slug }>{ cat.frontmatter.name }</DropdownItem>
                      ))
                    }
                  </DropdownMenu>
                </UncontrolledDropdown>

                <NavItem>
                  <NavLink className="link light" href="/contact/">Kom op ons lys</NavLink>
                </NavItem>
                <NavItem>
                  <NavLink className="link light" href="https://facebook.com/groups/221643825770134"
                           target="_blank" title="Our Facebook page" rel="nofollow noopener noreferrer"><FontAwesomeIcon icon={ faFacebook } /></NavLink>
                </NavItem>
              </Nav>
            </Collapse>
          </Navbar>
          {
            showTitle && !hideFacebook && (
            <div className="container-fluid">
              <div className="row">
                <div className="col-md facebook-wrapper">
                  <FacebookProvider appId="934192397029973">
                    <Like href="http://www.facebook.com/groups/221643825770134" colorScheme="light" showFaces share />
                  </FacebookProvider>
                </div>
              </div>
            </div>
            )
          }
        </header>
      );
    }
    }/>

  );
};

export default Header
