import React from 'react';
import { graphql, StaticQuery } from 'gatsby'
import CategoryIcon from './category-icon'
import classnames from 'classnames'

const Categories = ({ className }) => {
  return (
    <StaticQuery
      query={graphql`
      {
        categories: allMarkdownRemark(filter: {frontmatter: {type: {eq: "category"}}}, sort: {fields: [frontmatter___name]}) {
              edges {
                  node {
                      frontmatter {
                          tag
                          name
                          icon
                      }
                      fields {
                          slug
                      }
                  }
              }
          }
        }
      `}
      render={data => {
        const categories = data.categories.edges.map(edge => edge.node);
        return (
          <div className={classnames('content-list', className)}>
            <h2 className="title--header small primary-colour mb-3">KategorieÃ«</h2>
            <ul>
              {
                categories.map((cat, idx) => (
                  <li key={idx}>
                    <CategoryIcon path={cat.fields.slug} name={cat.frontmatter.name}
                                  icon={cat.frontmatter.icon}/><a href={cat.fields.slug}
                                                                   className="link"> {cat.frontmatter.name}
                  </a></li>
                ))
              }
            </ul>
          </div>
        );
      }}
    />
  );
};

export default Categories;