import React, { useState } from 'react'
import { Container, Row, Col, InputGroup, InputGroupAddon, Button } from 'reactstrap'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faSearch } from '@fortawesome/free-solid-svg-icons'
import PattyBot from './patty-bot'

const FixedChat = () => {

  const [ isOpen, setOpen ] = useState(false);

  return (
    <>
      { isOpen && <PattyBot close={() => setOpen(false)} /> }
      <Container fluid={ true } className="fixed-search p-3 m-0 bg-white search-shadow">
        <Row>
          <Col>
            <InputGroup>
              <div className="form-control pointer" onClick={ (e) => {e.preventDefault(); setOpen(true)} }>Soek in helpmekaar.org...</div>
              <InputGroupAddon addonType="append">
                <Button title="Soek" onClick={ (e) => {e.preventDefault(); setOpen(true)} }><FontAwesomeIcon icon={ faSearch } /></Button>
              </InputGroupAddon>
            </InputGroup>
          </Col>
        </Row>
      </Container>
    </>
  );
};

export default FixedChat;