import React, { useEffect, useRef } from 'react'
import { MessageList } from 'react-chat-elements'
import 'react-chat-elements/dist/main.css';
import useChatState from '../chat/use-chat-state'

const Chat = ({ userInput, onSearchResults, updateCount, setScrollToBottom }) => {

  const { dataSource, processInput, isTyping, sessionAttributes, newChatCount } = useChatState();

  const messageList = useRef(null);

  useEffect(() => {
    const node = document.querySelector('#is-typing');
    if (node) {
      node.remove();
    }
    if (isTyping) {
      setScrollToBottom(true);
      const existingNode = document.querySelector('#is-typing');
      if (!existingNode) {
        const newNode = document.createElement('div');
        newNode.setAttribute('id', 'is-typing')
        newNode.setAttribute('class', 'loading');
        messageList.current.mlistRef.appendChild(newNode);
      }
    }
  });

  useEffect(() => {
    if (!sessionAttributes.searchResults) {
      return;
    }
    onSearchResults(sessionAttributes.searchResults.results);
    setScrollToBottom(true);
  }, [sessionAttributes.searchResults])

  useEffect(() => {
    if (!userInput) {
      return;
    }
    processInput(userInput);
  }, [ userInput ]);

  useEffect(() => {
    if (!newChatCount) {
      return;
    }
    updateCount(newChatCount.count);
  }, [ newChatCount ]);

  return (
    <div className="chat">
      <MessageList
        ref={messageList}
        className="message-list h-100"
        lockable={ true }
        toBottomHeight="100%"
        dataSource={ dataSource }
      />
    </div>
  );
};

export default Chat;