import React from 'react'
import { Badge, Nav, NavItem, NavLink } from 'reactstrap'
import classnames from 'classnames'
import { faSearch } from '@fortawesome/free-solid-svg-icons'
import { faThList } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

const PattyBotChannels = ({ activeTab, setActiveTab, newChat, newSearch }) => {

  return (
    <Nav tabs>
      <NavItem>
        <div className="tab-container">
          { newSearch != null && newSearch !== 0 && <Badge className="notification-badge"><small>{ newSearch }</small></Badge> }
          <NavLink
            className={classnames({ active: activeTab === '2' })}
            onClick={() => { setActiveTab('2'); }}
          >
            <strong><FontAwesomeIcon icon={ faSearch } /> Soek</strong>
          </NavLink>
        </div>
      </NavItem>
      <NavItem>
        <NavLink
          className={classnames({ active: activeTab === '3' })}
          onClick={() => { setActiveTab('3'); }}
        >
          <strong><FontAwesomeIcon icon={ faThList } /> Inhoud</strong>
        </NavLink>
      </NavItem>
    </Nav>
  );
};

export default PattyBotChannels;