import React, { useEffect, useState } from 'react'
import { Modal, ModalBody, ModalFooter, ModalHeader, Row, TabContent, TabPane, Col } from 'reactstrap'
import ChatField from './chat-field'
import PattyBotChannels from './patty-bot-channels'
import Chat from './chat'
import Search from './search'
import Areas from './areas'
import Categories from './categories'
import TopThings from './top-things'
import HowToList from './how-to-list'

const PattyBot = ({ close }) => {

  const [ activeTab, setActiveTab ] = useState('2');
  const [ searchResults, setSearchResults ] = useState([]);
  const [ userInput, setUserInput ] = useState(null);
  const [ newChat, setNewChat ] = useState(null);
  const [ newSearch, setNewSearch ] = useState(null);
  const [ scrollToBottom, setScrollToBottom ] = useState(false);
  const [ scrollToTop, setScrollToTop ] = useState(false);

  const windowGlobal = typeof window !== 'undefined' && window;

  const resetNotificationCountAndActiveTab = tab => {
    if(tab === '2') {
      setNewSearch(0);
      setScrollToTop(true);
    } else if(tab === '3') {
      setScrollToTop(true);
    }
    setActiveTab(tab);
  };

  const updateChatCount = newCount => {
    if (activeTab === '1') {
      return;
    }
    setNewChat(newChat + newCount);
  };

  useEffect(() => {
    if (!scrollToTop) {
      return;
    }
    if (activeTab === '2') {
      if (windowGlobal) {
        windowGlobal.document.querySelector('.modal-body').scrollTop = 0;
      }
      setScrollToTop(false);
    }
  });

  useEffect(() => {
    if (!searchResults) {
      return;
    }
    if (activeTab === '2') {
      return;
    }
    setNewSearch(newSearch + searchResults.length);
  }, [ searchResults ]);

  useEffect(() => {
    if (!scrollToBottom) {
      return;
    }
    if (activeTab === '1') {
      if (windowGlobal) {
        const modalBody = windowGlobal.document.querySelector('.modal-body');
        modalBody.scrollTop = modalBody.scrollHeight + 150;
      }
      setScrollToBottom(false);
    }
  });

  return (
    <Modal isOpen={ true } className="patty-bot" fade={ false } autoFocus={ false }>
      <ModalHeader className="pb-0" toggle={ () => close() }>
        <PattyBotChannels
          activeTab={ activeTab }
          setActiveTab={ resetNotificationCountAndActiveTab }
          newChat={ newChat }
          newSearch={ newSearch } />
      </ModalHeader>
      <ModalBody>
        <TabContent activeTab={ activeTab }>
          <TabPane tabId="1">
            <Row>
              <Col md={{ size: 6, offset: 3}}>
                <Chat
                  onSearchResults={ setSearchResults }
                  userInput={ userInput }
                  updateCount={ updateChatCount }
                  setScrollToBottom={ setScrollToBottom }
                />
              </Col>
            </Row>
          </TabPane>
          <TabPane tabId="2">
            <Row>
              <Col md={{ size: 6, offset: 3}}>
                <Search results={ searchResults }/>
              </Col>
            </Row>
          </TabPane>
          <TabPane tabId="3">
            <Row>
              <Col md="3">
                <TopThings className="pb-5"/>
              </Col>
              <Col md="3">
                <HowToList className="pb-5"/>
              </Col>
              <Col md="3">
                <Areas className="pb-5"/>
              </Col>
              <Col md="3">
                <Categories className="pb-5"/>
              </Col>
            </Row>
          </TabPane>
        </TabContent>
      </ModalBody>
      <ModalFooter className="search-shadow-bottom">
        <ChatField onInput={ setUserInput } />
      </ModalFooter>
    </Modal>
  );
};

export default PattyBot;