import React from 'react';
import { graphql, StaticQuery } from 'gatsby'
import classnames from 'classnames'

const TopThings = ({ className }) => {
  return (
    <StaticQuery
      query={graphql`
      {
        topLists: allMarkdownRemark(filter: {frontmatter: {type: {eq: "category"}}}, sort: {fields: [frontmatter___name]}) {
            edges {
                node {
                    frontmatter {
                        tag
                        name
                        icon
                    }
                    fields {
                        slug
                    }
                }
            }
        }
      }
      `}
      render={data => {
        const topThings = data.topLists.edges.map(edge => edge.node);
        return (
          <div className={classnames('content-list', className)}>
            <h2 className="title--header small primary-colour mb-3">Top things to do</h2>
            <ul>
              {
                topThings.map((top, idx) => (
                  <li key={idx}><span className="spacer"/> <a href={top.fields.slug} className="link"> {top.frontmatter.name}</a></li>
                ))
              }
            </ul>
          </div>
        );
      }}
    />
  );
};

export default TopThings;