import React from 'react';
import { graphql, StaticQuery } from 'gatsby'
import classnames from 'classnames'

const Areas = ({ className }) => {
  return (
    <StaticQuery
      query={graphql`
      {
      areas: allMarkdownRemark(filter: {frontmatter: {type: {eq: "area"}}}, sort: {fields: [frontmatter___name]}) {
          edges {
            node {
              fields {
                slug
              }
              frontmatter {
                name
                tag
              }
            }
          }
        }
      }
      `
      }
      render={data => {
        const areas = data.areas.edges.map(edge => edge.node);
        return (<div className={classnames('content-list', className)}>
          <h2 className="title--header small primary-colour mb-3">Areas</h2>
          <ul>
            {
              areas.map((area, idx) => (
                <li key={idx}><span className="spacer"/> <a href={area.fields.slug} className="link">{area.frontmatter.name}</a></li>
              ))
            }
          </ul>
        </div>);
      }}
    />
  );
};

export default Areas;